.midsection {
    background: #fff;
    padding: 10px;
  }

.midsection-container {
    width: 800px;
    margin: 0 auto;
}

.insurance-button {
    position: relative;
    left: 12%;
}

.insuranceImage {
    width: 600px;
    background: #ffffff;
    margin-left: 200px;
}

.insurance {
    width: 200px;
    float: left;
}

.left-text {
    margin-bottom: 10px;
}

@media screen and (max-width: 960px) {
    .insuranceImage {
        width: 300px;
        background: #ffffff;
        margin-left: 200px;
    }
}