.image {
    position: relative;
    margin-left: 28%;
    width: 400px;
    height: 400px;
}

.image2 {
    position: relative;
    width: 400px;
    height: 400px;
}

.slide-container{
    background-color: #fff;
    margin: 0 auto;
    padding: 20px;
}

@media screen and (max-width: 960px) {
    .image {
        position: relative;
        margin-left: 12.5%;
        width: 300px;
        height: 300px;
    }
    
    .image2 {
        position: relative;
        width: 300px;
        height: 300px;
    }
    
    .slide-container{
        background-color: #fff;
        margin: 0 auto;
    }
}